* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
  padding-top: 0;
}

/* input {
  font-size: 1.3rem;
  width: 100%;
} */

ol {
  margin: unset !important;
  padding: unset !important;
 }

form{
  text-align: center;
}

.title-app {
  text-align: center;
  margin-bottom: 30px;
  font-size: 36px;
}

.title-category {
  text-align: center;
  font-size: 28px;
  text-transform: capitalize;
}

.searchGif {
  width: 95%;
  border: 2px solid #cbccce;
  padding: 10px;
  height: 30px;
  border-radius: 9px;
  outline: none;
  font-size: 1.3rem;
  color: #9DBFAF;
}

.searchGif:focus{
  color: #cbccce;
}

/* estilso para mostar los gifs en forma de card */
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
  width: 18%;
  align-content: center;
  border: 1px solid #cbccce;
  border-radius: 8px;
  margin-bottom: 30px;
  overflow: hidden;
  box-shadow: 0 1px 4px rgba(0,0,0,0.16);
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width:100%;
  height:170px;
  object-fit: cover;
/*Scale down will take the necessary specified space that is 100px x 100px without stretching the image*/
   /* object-fit:scale-down; */

}

/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/
@media (min-width: 768px) and (max-width: 1024px) {
  .card {
    width: 48%;

  }
}

@media screen and (max-width: 575px) {
  .card-grid {
    flex-direction: column;
  }
  .card {
    width: 100%;

  }
}